.App {
  font-family: Calibri;
  text-align: center;
}

#course-table {
  /* font-family: Calibri, "Trebuchet MS", Arial, Helvetica, sans-serif; */
  font-family: Calibri;
  font-size: 11px;
  margin-left: auto;
  margin-right: auto;
  border: 1px solid #e9e9e9;
  border-radius: 4px;
  width: 60%;
  border-collapse: collapse;
}

#course-table td {
  width: 33%;
  border: 1px solid;
}

#course-table tr {
  border: 1px solid;
}

#add-course {
  border: 1px solid;
  border-radius: 2px;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  height: 150px;
}

#advs {
    /* width: 60%; */
    width: auto;
    height: 80px;
    padding: 5px;
}

.square {
  /* width: 60%; */
  width: auto;
  height: 150px;
  padding: 5px;
}

.tag {
  position: relative;
  margin: 3px;
  font-size: 11px;
  border: 1px dashed #cccccc;
  border-radius: 4px;
  padding: 0 8px;
  line-height: 30px;
  color: #666666;
  background: rgba(255, 255, 255, 0.7);
}

.delete {
  position: absolute;
  top: -1px;
  right: -1px;
  width: 16px;
  height: 16px;
  cursor: pointer;
  user-drag: none;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
  -ms-user-select: none;
}

.inputs {
  margin-top: 10px;
}

input {
  position: relative;
  top: -1px;
  height: 23px;
}

button {
  margin-left: 10px;
  margin-bottom: 10px;
  background-color: #3789c7;
  border: none;
  color: white;
  padding: 5px 15px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 2px;
}
